<template>
  <div>
    <!-- 项目详情-->
    <div style="margin-bottom:10px;" v-if="url == 'info'">
      <el-button size="mini" @click="goBack" icon="el-icon-back">项目列表</el-button>
    </div>
    <el-tabs v-model="activeName" type="border-card" @tab-click="handleClick" v-if="url == 'info'">
      <el-tab-pane label="配置文件" name="first">

        <!-- 配置文件 -->
        <div style="margin: 15px 0;">项目名称</div>
        <el-form class="query-form" inline size="small" label-width="80px" :model="searchProduct">
          <el-formItem prop="word" style="width: 20%">
              <el-input v-model="searchProduct.word" />
            </el-formItem>
            <div class="action-groups">
              <el-button type="primary" plain @click="" size="small">查询</el-button>
              <el-button type="primary" plain @click="" size="small">重置</el-button>
            </div>


            <el-row>
              <el-col :span="24">
                <!-- 项目详细信息 -->
                <el-table :data="configList" style="width: 100%" class="select-from" :stripe="true"
                  :header-cell-style="{ textAlign: 'center' }" :cell-style="{ textAlign: 'center' }">
                  <ElTableColumn type="selection" />
                  <el-table-column   prop="fileName" > 
                    <template slot-scope="scope">
                      
                        <el-link type="primary ">{{ scope.row.fileName}}</el-link>
                    </template>
                  </el-table-column>
                  <el-table-column   prop="message" > </el-table-column>
                </el-table>
              </el-col>
            </el-row>

            <!--分页-->
            <el-row class="page-row">
              <el-pagination class="el-pagination" layout="total, sizes, prev, pager, next, jumper"
                :page-sizes="[5, 10, 30, 50]" @size-change="sizeChange" @current-change="currentChange"
                @prev-click="currentChange" @next-click="currentChange" :current-page="current" :page-size="size"
                :total="total">
              </el-pagination>
            </el-row>

          </el-form>
        </el-tab-pane>
        <el-tab-pane label="项目详情" name="second">
          <!-- 项目详情 -->
          <!-- <el-form class="query-form" inline size="small" label-width="80px" :model="searchProduct">
          <div style="margin: 15px 0;">项目名称</div>
            <el-formItem prop="word" style="width: 20%">
              <el-input v-model="searchProduct.word" />
            </el-formItem>
            <div class="action-groups">
              <el-button type="primary" plain @click="" size="small">查询</el-button>
              <el-button type="primary" plain @click="" size="small">重置</el-button>
            </div>
          </el-form> -->
          <Report></Report>
        </el-tab-pane>

      </el-tabs>


    <!--项目列表-->
    <el-card style="margin-top: 20px" v-if="url == 'project'">
      <div style="margin: 15px 0;">项目列表</div>
      <el-row>
        <el-col :span="24">
          <!-- 新增按钮 -->
          <el-header style="background-color: #ffffff; margin-left: -5px">
            <!-- <el-button @click="handleSave()" icon="el-icon-circle-plus-outline">新增项目</el-button> -->
            <el-form class="query-form" inline size="mini" label-width="120px" :model="searchProduct">
              <el-form-item>
                <el-input placeholder="" class="input" @focus="" style="border-radius: 14px !important;">
                  <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
              </el-form-item>
            </el-form>
          </el-header>
          <!-- 项目详细信息 -->
          <el-table :data="dataList" style="width: 100%" class="select-from" :stripe="true"
            :header-cell-style="{ textAlign: 'center' }" :cell-style="{ textAlign: 'center' }">
            <ElTableColumn type="selection" />
            <el-table-column sortable width="80" prop="id" label="ID"> </el-table-column>
            <el-table-column sortable width="210" prop="name" label="项目名称"> </el-table-column>
            <el-table-column width="210" prop="level" :filters="filterArr">
              <template slot="header" slot-scope="{ column }">
                <span slot="content">评级</span>
                <span>{{ column.label }}<i class="co-icon-filter" /></span>
              </template>
            </el-table-column>
            <el-table-column sortable width="210" prop="progress" label="进度">
              <template slot-scope="scope">
                <el-progress type="line" :percentage="scope.row.progress" :stroke-width="12"
                  style="width: 150px;margin: 0 auto;"></el-progress>
              </template>
            </el-table-column>
            <el-table-column width="210" prop="technology" :filters="filterArr2">
              <template slot="header" slot-scope="{ column }">
                <span slot="content">技术</span>
                <span>{{ column.label }}<i class="co-icon-filter" /></span>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="300">
              <template slot-scope="scope">
                <el-button style="margin: 0 0 0 0" icon="el-icon-setting" size="mini" type="primary"
                  @click="setDialog(scope.row.id, scope.row.title, scope.row.price)" slot="reference">详情
                </el-button>
                <el-popconfirm :title="'确定删除' + scope.row.name + '吗？'" @confirm="remove(scope.row.id)">
                  <el-button style="margin: 0 0 0 15px" icon="el-icon-close" size="mini" type="danger" slot="reference">删除
                  </el-button>
                </el-popconfirm>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>

      <!--分页-->
      <el-row class="page-row">
        <el-pagination class="el-pagination" layout="total, sizes, prev, pager, next, jumper"
          :page-sizes="[5, 10, 30, 50]" @size-change="sizeChange" @current-change="currentChange"
          @prev-click="currentChange" @next-click="currentChange" :current-page="current" :page-size="size"
          :total="total">
        </el-pagination>
      </el-row>
    </el-card>

    <!-- 添加 -->
    <el-dialog title="添加项目" :visible.sync="saveDialog" width="600px">
      <el-form :model="productObj" ref="ruleForm" label-width="80">
        <el-form-item label="项目名称" prop="title" :rules="[{ required: true, message: '名称不能为空' }]">
          <el-input v-model="productObj.title" maxlength="8" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="价格(分)" prop="price" :rules="[
          { required: true, message: '价格不能为空' },
          { type: 'number', message: '价格必须为数字值' },
        ]">
          <el-input v-model.number="productObj.price"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" style="width: 255px; margin: 45px 0 0 65px" @click="productAdd"
            :disabled="productObj.title === '' || productObj.price === null || productSet.price === ''">立即添加
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- 编辑 -->
    <el-dialog title="编辑项目" :visible.sync="updateDialog" width="600px">
      <el-form :model="productSet" ref="ruleForm" label-width="80px">
        <el-form-item label="项目ID" prop="id">
          <el-input v-model="productSet.id" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="项目名称" prop="title" :rules="[{ required: true, message: '名称不能为空' }]">
          <el-input v-model="productSet.title" maxlength="8" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="价格(分)" prop="price" :rules="[
          { required: true, message: '价格不能为空' },
          { type: 'number', message: '价格必须为数字值' },
        ]">
          <el-input v-model.number="productSet.price"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" style="width: 255px; margin: 45px 0 0 65px" @click="updateProduct"
          :disabled="productSet.title === '' || productSet.price === null || productSet.price === ''">确定修改
        </el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</div>
</template>

<script>
  import Report  from "../report/index";
export default {
  components: {Report},
  data() {
    return {
      adminName: "",
      // 搜索条件对象
      searchProduct: {
        id: "",
      },
      // 项目数据 (页面初始化时会将数据传进来)
      dataList: [
        // 静态数据
        { id: 9527, name: "项目1", level: '评级A', progress: 100, technology: "C/C++,C#" },
        { id: 9528, name: "项目2", level: '评级B', progress: 50, technology: "C/C++,JAVA" },
        { id: 9529, name: "项目3", level: '评级C', progress: 70, technology: "JAVA" },
      ],
      // 配置数据
      configList: [
        { fileName: 'fileAgent', message: "fileAgent" },
        { fileName: 'pams', message: 'pams' },
        { fileName: 'res', message: '4 of 4 shown' },
      ],
      infoList: [
        // 静态数据
        { id: 9527, name: "项目1", level: '评级A', progress: 1, technology: "C/C++,C#" },
        { id: 9528, name: "项目2", level: '评级B', progress: 0.5, technology: "C/C++,JAVA" },
        { id: 9529, name: "项目3", level: '评级C', progress: 0.7, technology: "JAVA" },
      ],
      size: 5, // 每页的数据大小
      current: 1, //当前页数
      total: 0, // 总数量
      // 新增项目
      saveDialog: false, // 是否显示
      // 添加的项目对象
      productObj: {
        title: "",
        price: null,
      },
      // 编辑项目
      updateDialog: false, // 是否显示
      // 编辑的项目对象
      productSet: {
        id: null,
        title: "",
        price: null,
      },
      // option
      filterArr: [
        { text: "评级A", value: "A" },
        { text: "评级B", value: "B" },
        { text: "评级C", value: "C" },
      ],
      filterArr2: [
        { text: "C", value: "下拉1" },
        { text: "C++", value: "下拉2" },
        { text: "C#", value: "下拉2" },
        { text: "JAVA", value: "下拉2" },
      ],
      activeName: 'first',

      // 项目详情
      url:'project'
    };
  },
  created() {
    this.getData();
  },
  methods: {
    // 添加框
    handleSave() {
      this.saveDialog = true;
    },
    // 编辑框
    setDialog(id, title, price) {
      this.productSet.id = id;
      this.productSet.title = title;
      this.productSet.price = price;
      // this.updateDialog = true;

      // this.$router.push('/project-info')
      this.url = 'info';
    },
    filterChange(filters) {
      // 判断是哪一列筛选
      if (filters.id) {
        //  赋值这一列绑定的值
        this.dataList.id = filters.id[0];
      }
      //  this.getTableList(); // 通过接口获取数据的方法
    },
    // 删除
    remove(id) { },
    // 立即添加
    productAdd() { },
    // 更新项目信息
    updateProduct() { },
    // 每页大小修改
    sizeChange(val) {
      this.size = val;
      if (this.searchProduct.productName !== "") {
        this.search();
      } else {
        this.getData();
      }
    },
    // 根据搜索条件查询
    search() {
      if (this.searchProduct.productName !== "") {
      } else {
        this.$message.error("搜索的项目名不能为空");
      }
    },
    // 第几页
    currentChange(val) {
      this.current = val;
      if (this.searchProduct.productName !== "") {
        this.search();
      } else {
        this.getData();
      }
    },
    // 获取数据，并赋值给wordData
    getData() { },
    handleClick() {

    },
    goBack(){
       this.url = "project";
    }

  },
};
</script>

<style>
.page-row {
  margin-top: 20px;
  text-align: center;
}

.header2 {
  color: #004b54;
  font-size: 20px;
  position: absolute;
  left: 10px;
  top: 5px;
}

.action-groups {
  display: inline-block;
}


/*搜索组件最外层div */
.input_box {
  width: 300px;
  margin-right: 15px;
  border-radius: 95px;
  background: rgb(0 234 245 / 48%);
}

/*搜索input框 */
:deep(.el-input__wrapper) {
  background-color: transparent;
  /*覆盖原背景颜色，设置成透明 */
  border-radius: 95px;
  border: 0;
  box-shadow: 0 0 0 0px;
}

/*搜索button按钮 */
:deep(.el-input-group__append) {
  background: rgb(0 234 245 / 48%);
  border-radius: 95px;
  border: 0;
  box-shadow: 0 0 0 0px;
}
</style>
